<template>
  <div class="tabListContent">

    <van-sticky :offset-top="'1.72rem'">
      <van-dropdown-menu active-color="#41ae3c" class="dropMenu">
        <van-dropdown-item v-model="pxValue" :options="pxOptions" @change="handelOptions"/>
        <van-dropdown-item v-model="lxValue" :options="lxOptions" @change="handelOptions"/>
        <van-dropdown-item v-model="dqValue" :options="dqOptions" @change="handelOptions"/>
        <van-dropdown-item v-model="yyValue" :options="yyOptions" @change="handelOptions"/>
        <van-dropdown-item v-model="nfValue" :options="nfOptions" @change="handelOptions"/>
      </van-dropdown-menu>
    </van-sticky>
    
    <van-pull-refresh 
      v-model="refreshing" 
      @refresh="onRefresh"
    >
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
        >
          <!-- 九宫格数据列表 -->
          <div class="vod-container">
            <van-grid
              :column-num="3"
              :border="false"
              >
              <van-grid-item 
                v-for="vod in vods" 
                :key="vod.id"
                >
                <div 
                  class="image-container" 
                  :style="vimageStyle"
                  @click="handleToDetail(vod.id)"
                  >
                  <img v-lazy="vod.imgy" class="fixed-size-image" />
                  <span class="span-mark">{{ vod.remarks }}</span>
                </div>
                <span class="vod-name" :style="vnameStyle">
                  {{ vod.name }}
                </span>
              </van-grid-item>
            </van-grid>
          </div>
      
        </van-list>
    </van-pull-refresh>

  </div>
</template>

<script>
import { List, PullRefresh,DropdownMenu, DropdownItem,Grid, GridItem,Sticky} from 'vant'
import 'vant/lib/dropdown-menu/style'
import 'vant/lib/dropdown-item/style'
import 'vant/lib/list/style'
import 'vant/lib/pull-refresh/style'
import 'vant/lib/grid/style'
import 'vant/lib/sticky/style'
import 'vant/lib/grid-item/style'

export default {
  name:'TabVodList',
  components: {
    'van-dropdown-menu': DropdownMenu,
    'van-dropdown-item': DropdownItem,
    'van-list': List,
    'van-pull-refresh': PullRefresh,
    'van-grid': Grid, 
    'van-grid-item': GridItem,
    [Sticky.name]:Sticky,
  },
  props: {
    channel:Object
  },
  data() {
    return {
      loading: false,
      finished: false,
      refreshing: false,
      page: 1,
      pxValue: '热门',
      lxValue: '类型',
      dqValue: '地区',
      yyValue: '语言',
      nfValue: '年份',
      vods: [],
    }
  },
  methods:{
    onRefresh () {
      this.refreshing = true
      this.loading = false
       // 清空列表数据
      this.vods = []
      this.finished = false
      this.page = 0
      
      // 重新加载数据
      this.onLoad()
    },
    onLoad () {
      // 异步更新数据
      if (this.refreshing) {    
        this.refreshing = false
      }
      this.page += 1
      // 将 loading 设置为 true，表示处于加载状态
      this.loading = true
      this.getVods()
    },
     getVods () {
      this.$http.get('api/pdsx?pd='+this.pd+
      '&px='+this.pxValue+
      '&lx='+(this.lxValue === '类型' ? '' : this.lxValue)+
      '&dq='+(this.dqValue === '地区' ? '' : this.dqValue)+
      '&yy='+(this.yyValue === '语言' ? '' : this.yyValue)+
      '&nf='+(this.nfValue === '年份' ? '' : this.nfValue)+
      '&page='+this.page+
      '&limit=12')
          .then(this.vodsSucc)
     },
     vodsSucc (res) {
        console.log(res)
        res = res.data
        if (res.code === 1000 && res.data) {
           const data = res.data
          if (data.length > 0) {
            this.vods.push(...data)
          } else {
            // 数据全部加载完成
            this.finished = true
          }
        }
        // 加载状态结束
        console.log('this.vods: '+this.vods.length)
        this.loading = false
     },
     handelOptions () {
        this.page = 1
        this.vods = []
        this.getVods()
     },
     handleToDetail (id) {
      this.$router.push({ path: `/detail/${id}` })
     }
  },
  mounted () {
    // console.log('channel: '+ this.channel.lx)   
    this.getVods()
  },
  computed: {
    vnameStyle () {
      let widthtep = 2.34
      if (this.$utils.vodlistWidth > 0) {
        widthtep = this.$utils.vodlistWidth
      }
      return {
        width: widthtep + 'rem'
      }
    },
    vimageStyle() {
      let widthtep = 2.34 
      let heighttep = 3.11 
      if (this.$utils.vodlistWidth > 0) {
        widthtep = this.$utils.vodlistWidth
        heighttep = this.$utils.vodlistHeight
      }
      return {
        width: widthtep + 'rem',
        height: heighttep + 'rem'
      }
    },
    pd () {
      return this.channel.id
    },
    pxOptions () {
      const pxArr = this.channel.px.split(',')
      let pxList = []
      pxArr.forEach((element) => {
          pxList.push({
            text:element,
            value:element
          })
      })
      return pxList
    },
    lxOptions () {
      const lxArr = this.channel.lx.split(',')
      let lxList = [{
        text:'类型',
        value:'类型'
      }]
      lxArr.forEach((element) => {
        lxList.push({
            text:element,
            value:element
          })
      })
      return lxList
    },
    dqOptions () {
      const dqArr = this.channel.dq.split(',')
      let dqList = [{
        text:'地区',
        value:'地区'
      }]
      dqArr.forEach((element, index) => {
          dqList.push({
            text:element,
            value:element
          })
      })
      return dqList
    },
    yyOptions () {
      const yyArr = this.channel.yy.split('、')
      let yyList = [{
        text:'语言',
        value:'语言'
      }]
      yyArr.forEach((element, index) => {
          yyList.push({
            text:element,
            value:element
          })
      })
      return yyList
    },
    nfOptions () {
      const nfArr = this.channel.nf.split(',')
      let nfList = [{
        text:'年份',
        value:'年份'
      }]
      nfArr.forEach((element, index) => {
          nfList.push({
            text:element,
            value:element
          })
      })
      return nfList
    }
  }
}
</script>

