<template>
  <div class="home-page">
    <home-header></home-header>
    <home-tab-list 
      :channelList="channelList"
      >
    </home-tab-list>
  </div>
</template>

<script>
import HomeHeader from './components/Header.vue';
import HomeTabList from './components/TabList.vue'

export default {
    name: 'HomePage',
    components: {
      HomeHeader,
      HomeTabList
    },
    data () {
       return {
         channelList:[]
       }
    },
    methods: {
      getChannels() {
        this.$http.get('/api/type')
          .then(this.channelSucc)
      },
      channelSucc(res) {
        // console.log(res)
        res = res.data
        if(res.code == 1000 && res.data){
          const data = res.data
          // this.channelList.concat(data)
          //push 一下刷新
          this.channelList.push({
            id:0,
            mz:'推荐',
            dq:'',
            lx:'',
            nf:'',
            px:'',
            yy:''
          })
          // Array.prototype.push.apply(this.channelList, data)
          this.channelList.push(...data)
          console.log(this.channelList)
        }
      },
      getpz() {
        this.$http.get(`/api/pz?app=${this.$utils.app}&name=${this.$utils.app}_data`)
          .then((res)=>{
            const result = res.data
            if(result.code === 1000 && result.data){
              try {
                const dataObject = JSON.parse(result.data)
                this.$utils.config.gw = dataObject.gw
                this.$utils.config.shareText = dataObject.shareText
                this.$utils.config.adShow = dataObject.adShow
                this.$utils.config.adTime = dataObject.adTime
                // console.log(this.$utils.config.gw)
              } catch (error) {
                console.error(error)
              }
            }
          })
      },
    },
    mounted () {
      this.getChannels()
      this.getpz()
      console.log('屏幕:', this.$utils.screenWidth, this.$utils.screenHeight)
    }
}
</script>

<style lang="stylus" scoped>
  .home-page
    overflow hidden
</style>