<template>
  <div class="tab-recommend">
    <van-pull-refresh
      v-model="refreshing" 
      @refresh="onRefresh"
    >
      <div class="swipe-container">
        <van-swipe
          class="my-swipe"
          :autoplay="3000"
          indicator-color="white"
          :show-indicators="false"
          @change="onChange"
          >
            <van-swipe-item 
              v-for="(banner) in banners" 
              :key="banner.url"
              @click="handleSwipeItem(banner.url)"
              >
                <img class="swip-img" v-lazy="banner.img" />
                <span class="banner-title">{{ banner.title }}</span>
            </van-swipe-item>
        </van-swipe>

        <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
        >
          <!-- 九宫格数据列表 -->
          <div v-for="topic of limitedTopicList" :key="topic.id">
            <div class="topic-title" @click="toTopic(topic.id,topic.title)">
              <span class="topic-title-left">{{ topic.title }}</span>
              <van-icon class="topic-title-right" name="arrow" />
            </div>

            <van-grid
              :column-num="3"
              :border="false"
              >
              <van-grid-item 
                v-for="vod in topic.vod" 
                :key="vod.id"
                >
                <div 
                  class="image-container" 
                  :style="imageStyle"
                  @click="handleToDetail(vod.id)"
                  >
                  <img v-lazy="vod.imgy" class="fixed-size-image" />
                  <span class="span-mark">{{ vod.remarks }}</span>
                </div>
                <span class="vod-name" :style="nameStyle">
                  {{ vod.name }}
                </span>
              </van-grid-item>
            </van-grid>
          </div>
      
        </van-list>

      </div>
    </van-pull-refresh>
  </div>
</template>

<script>
import { List, PullRefresh, Swipe, SwipeItem,Grid, GridItem,Image as VanImage,Icon } from 'vant'
import 'vant/lib/list/style'
import 'vant/lib/pull-refresh/style'
import 'vant/lib/swipe/style'
import 'vant/lib/swipe-item/style'
import 'vant/lib/grid/style'
import 'vant/lib/icon/style'
import 'vant/lib/grid-item/style'

export default {
   name:'TabRecommend',
   components: {
    'van-list': List,
    'van-pull-refresh': PullRefresh,
    'van-swipe': Swipe,
    'van-swipe-item': SwipeItem,
    'van-grid': Grid, 
    'van-grid-item': GridItem,
    'van-image': VanImage,
    [Icon.name]:Icon,
   },
   data() {
    return {
      topicList: [],
      page: 0,
      loading: false,
      finished: false,
      refreshing: false,
      banners: [],
    }
  },
  methods: {
    onRefresh () {
      this.refreshing = true
      this.loading = false
       // 清空列表数据
      this.topicList = []
      this.finished = false
      this.page = 0
      
      // 重新加载数据
      this.onLoad()
    },
    onLoad () {
      // 异步更新数据
      if (this.refreshing) {    
        this.refreshing = false
      }
      this.page += 1
      // 将 loading 设置为 true，表示处于加载状态
      this.loading = true
      this.getTopicByPage()
    },
    onChange(index) {
      // console.log('当前 Swipe 索引：' + index)
    },
    handleSwipeItem (bannerUrl) {
      console.log('当前 bannerUrl：' + bannerUrl)
      if(bannerUrl.indexOf('//vod/') !== -1) {
        const id = bannerUrl.replace('//vod/','')
        this.$router.push({ path: `/detail/${id}` })
      } else {
        window.location.href = 'jdzj://function?opengw='+bannerUrl
      }
    },
    getBanner () {
      this.$http.get('/api/datu')
          .then(this.bannerSucc)
    },
    bannerSucc (res){
      console.log(res)
      res = res.data
      if(res.code == 1000 && res.data){
        const data = res.data
        this.banners = data
      }
    },
    getTopicByPage() {
      this.$http.get('api/tjjj?page='+this.page+'&limit=3')
          .then(this.topicSucc)
    },
    topicSucc (res) {
      res = res.data
      if(res.code == 1000 && res.data){
        const data = res.data
        if (data.length > 0){
          this.topicList.push(...data)
        } else {
          // 数据全部加载完成
          this.finished = true
        }
      }
      // 加载状态结束
      this.loading = false
    },
    handleToDetail (id) {
      this.$router.push({ path: `/detail/${id}` })
    },
    toTopic(tid,title) {
      this.$router.push({ path: `/topic/${tid}/${title}` })
    }
  },
  computed: {
    nameStyle () {
      let widthtep = 2.34
      if (this.$utils.vodCoverWidth > 0) {
        widthtep = this.$utils.vodCoverWidth
      }
      return {
        width: widthtep + 'rem'
      }
    },
    imageStyle() {
      let widthtep = 2.34 
      let heighttep = 3.11 
      if (this.$utils.vodCoverWidth > 0) {
        widthtep = this.$utils.vodCoverWidth
        heighttep = this.$utils.vodCoverHeight
      }
      return {
        width: widthtep + 'rem',
        height: heighttep + 'rem'
      }
    },
    limitedTopicList() {
      // 使用map函数处理topicList，返回每个主题包含前6个视频的新数组
      return this.topicList.map(topic => {
        // 创建topic的浅拷贝以避免直接修改原始数据
        const topicCopy = { ...topic }
        // 使用slice限制vod数组只包含前6个元素
        topicCopy.vod = topicCopy.vod.slice(0, 6)
        return topicCopy
      })
    }
  },
  mounted () {
    this.getBanner()
    // this.getTopicByPage()
    // 注册resize事件监听器，当屏幕大小变化时更新尺寸数据
    // window.addEventListener('resize', this.getScreenSize)
  },
  // beforeDestroy() {
  //   // 在组件销毁之前移除resize事件监听器
  //   window.removeEventListener('resize', this.getScreenSize)
  // }
}
</script>

